import react, {Component} from 'react';
import Layout from '../components/Layout'
import fetch from 'isomorphic-unfetch';
import {Container, Control, Input, Icon, Title, Panel, PanelBlock, PanelHeading, PanelTab, PanelTabs} from 'bloomer'


const FFCApi ='https://ffc-server.herokuapp.com/api/teams';

const bg ={
    background: 'white',
    height: '100%',
    marginTop: '8px'
}



export default class Teams extends Component{
    static async getInitialProps(){
        const res = await fetch(FFCApi);
        const data = await res.json()
        console.log(`Show data fetched. Count: ${data.length}`)
        return {teams: data}

    }
    render(){
        return(<Layout>
            <Container>
        <div style = {bg} >
        <Title>Teams</Title>
        <Panel>
            <PanelHeading>Select players</PanelHeading>
            <PanelBlock>
        <Control hasIcons='left'>
            <Input isSize='small' placeholder='Search' />
            <Icon isSize='small' isAlign='left'>
                <span className='fa fa-search' aria-hidden='true' />
            </Icon>
        </Control>
    </PanelBlock>
        
    <PanelTabs>
        <PanelTab isActive>All</PanelTab>
        {this.props.teams.map((index)=>(
            <PanelTab>{index.name}</PanelTab>
        ))}

    </PanelTabs>

        </Panel>
        </div>
        </Container>
        
        </Layout>)

    }
}


